.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 10%;
    background-color: #000;
    color: #fff;
    position: relative;
  }
  
  .header-title {
    display: flex;
    align-items: center;
  }
  
  .header-title h1 {
    font-size: 1.8em;
    font-weight: bold;
    color: #fff;
    margin-left: 10px;
  }
  
  .avatar {
    width: 40px; /* Adjust size as needed */
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .header nav a {
    margin: 0 10px;
    text-decoration: none;
    color: #fff;
    transition: color 0.3s ease;
  }
  
  /* Other styles (media queries, etc.) remain unchanged */
  